<template>
    <div class="login">
        <h1>PlatON官网运营后台系统</h1>
        <el-form label-width="60px" :model="loginForm" :rules="rules" ref="loginForm">
            <el-form-item label="账号" prop="loginName">
                <el-input v-model="loginForm.loginName" placeholder="请输入账号" auto-complete="off" @keyup.native.enter="login()"></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
                <el-input type="password" v-model="loginForm.password" placeholder="请输入密码" auto-complete="new-password" @keyup.native.enter="login()"></el-input>
            </el-form-item>
        </el-form>
        <el-button type="primary" @click="login">登录</el-button>
    </div>
</template>

<script>
    import r from '../../../static/js/RequestUtil.js'
    import Cookie from '../../../static/js/cookie.js'
    import MD5 from 'crypto-js/md5';
    export default {
        data() {
            return {
                loginForm: {
                    loginName: null,
                    password: null,
                },
                rules: {
                    loginName: [{
                            required: true,
                            message: '请输入账号',
                            trigger: 'blur'
                        },
//                      {
//                          min: 6,
//                          max: 18,
//                          message: '长度在 6 到 18 个字符',
//                          trigger: 'blur'
//                      }
                    ],
                    password: [{
                            required: true,
                            message: '请输入密码',
                            trigger: 'blur'
                        },
                        {
                            min: 6,
                            max: 18,
                            message: '长度在 6 到 18 个字符',
                            trigger: 'blur'
                    }]
                }
            }
        },
        methods: {
            login() {
                var _this = this;
                _this.$refs.loginForm.validate((valid) => {
                    if(valid) {
                        var param = {
                            loginName: _this.loginForm.loginName,
                            password: MD5(_this.loginForm.password).toString(),
                        }
                        r.post("spring-mvc/login/login.do", param, function(data) {
                            if(data && data.code === 0) {
                                console.log(data);
                                _this.$router.replace('zhnews');
                                Cookie.set('userInfo',JSON.stringify(param));
                            }else if(data && data.code === -1){
                                r.message("登录失败", "warning");
                            }else{
                                r.message(data.message, "warning");
                            }
                        })
                    } else {
                        return false;
                    }
                });
            }
        },
        mounted(){
//          r.post("spring-mvc/common/findMenu.do",function(data){
//
//          })
        }
    }
</script>

<style scoped>
    .login {
        width: 400px;
        background-color: #fff;
        padding: 30px 20px;
        border-radius: 5px;
        position: fixed;
        top: 20%;
        left: 35%;
        text-align: center;
        border: 1px solid #eee;
    }

    h1 {
        font-size: 32px;
        color: #666;
        margin-bottom: 50px;
        text-align: center;
    }
</style>


