<template>
    <div>
        <!--head-->
        <div class="content-c-breadcrumb">
            <el-breadcrumb separator="-">
                <el-breadcrumb-item>新闻管理</el-breadcrumb-item>
                <el-breadcrumb-item>中文版本</el-breadcrumb-item>
            </el-breadcrumb>
        </div>

        <!--操作按钮-->
        <div class="content-util">
            <el-row :gutter="20">
                <el-col :span="18">
                    <el-button type="primary">新建</el-button>
                    <el-button type="primary" :disabled="invalid">上移</el-button>
                    <el-button type="primary" :disabled="invalid">下移</el-button>
                </el-col>
                <el-col :span="6">
                    <el-input placeholder="输入新闻标题关键字搜索" v-model="searchValue">
                        <el-button slot="append" icon="el-icon-search"></el-button>
                    </el-input>
                </el-col>
            </el-row>
        </div>
        <!--表格数据-->
        <el-table ref="selectDate" class="table-m20" v-loading.body="loading" :data="listData" border @selection-change="selectionChange" style="width:100%;height:500px;">
            <el-table-column type="selection" width="50"></el-table-column>
            <el-table-column prop='num' label="序号"></el-table-column>
            <el-table-column prop='title' label="新闻标题"></el-table-column>
            <el-table-column prop='user' label="创建人"></el-table-column>
            <el-table-column prop='time' label="创建时间"></el-table-column>
            <el-table-column prop='statusText' label="状态"></el-table-column>
            <el-table-column label="操作" fixed="right" width="240">
                <template slot-scope="scope">
                    <el-button type="text" v-if="scope.row.status==1">撤回</el-button>
                    <el-button type="text" v-if="scope.row.status==1">查看</el-button>
                    <el-button type="text" v-if="scope.row.status==0">发布</el-button>
                    <el-button type="text" v-if="scope.row.status==0">预览</el-button>
                    <el-button type="text">记录</el-button>
                    <el-button type="text">编辑</el-button>
                    <el-button type="text" :disabled="scope.row.status==1">删除</el-button>
                </template>
            </el-table-column>
        </el-table>
        <!--分页-->
        <div class="pager-wrap content-pages">
            <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="page.currentNo" :page-sizes="[10, 20, 50, 100]" :page-size="page.pageSize" layout="total, sizes, prev, pager, next, jumper" :total="page.totalPages">
            </el-pagination>
        </div>

    </div>
</template>

<script>
    export default {
        data() {
            return {
                searchValue: '',
                loading: false,
                invalid:true,
                selectDate:[],
                listData: [{
                    num:1,
                    title:'标题',
                    user:'admin',
                    time:'2018-08-13',
                    status:1,
                    statusText:'已发布'
                },{
                    num:2,
                    title:'标题',
                    user:'admin',
                    time:'2018-08-13',
                    status:0,
                    statusText:'未发布'
                }],
                page: {
                    totalPages: 100,
                    currentNo: 1,
                    pageSize: 50
                },
            }
        },
        methods: {
            selectionChange(val) {
                this.selectDate = val;
                if(this.selectDate.length !== 0){
                    this.invalid = false;
                }else{
                    this.invalid = true;
                }
            },
            handleSizeChange(val) {
                this.page.pageSize = val;
            },
            handleCurrentChange(val) {
                this.page.currentNo = val;
            },
        }
    }
</script>

<style>

</style>


