<template>
    <div class="main">
        <!--头部-->
        <header class="header">
            <div class="head-left">
                PlatON官网运营后台
            </div>
            <div class="head-right">
                <div class="right-content">
                    <span>{{loginName}}，你好</span>
                    <el-button class="ml50 mr20" @click="modifyPassword" type="text">修改密码</el-button>
                    <el-button type="text" @click="loginOut">退出</el-button>
                </div>
            </div>
        </header>
        <!--侧边栏-->
        <article class="content">
            <nav class="content-nav">
                <el-menu default-active="1-1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" background-color="#324157" text-color="#fff">
                    <el-submenu index="1">
                        <template slot="title">
                            <i class="el-icon-location"></i>
                            <span>新闻管理</span>
                        </template>
                        <el-menu-item index="1-1">中文版本</el-menu-item>
                        <el-menu-item index="1-2">英文版本</el-menu-item>
                    </el-submenu>
                    <el-menu-item index="2">
                        <i class="el-icon-menu"></i>
                        <span slot="title">FAQ管理</span>
                    </el-menu-item>
                    <el-menu-item index="3">
                        <i class="el-icon-document"></i>
                        <span slot="title">订阅管理</span>
                    </el-menu-item>
                </el-menu>
            </nav>
            <section class="content-content">
                <router-view></router-view>
            </section>
        </article>
        <!--修改密码-->
        <el-dialog title="修改密码" :visible.sync="modifyPadDialog" width="30%" :before-close="resetForm">
            <el-form :model="ruleForm" status-icon :rules="rules" ref="ruleForm" label-width="80px">
                <el-form-item label="旧密码" prop="oldpassword">
                    <el-input type="password" v-model="ruleForm.oldpassword" placeholder="请输入旧密码" auto-complete="off" oncopy="return false;" onpaste="return false;" oncut="return false;"></el-input>
                </el-form-item>
                <el-form-item label="新密码" prop="newpassword">
                    <el-input type="password" v-model="ruleForm.newpassword" placeholder="请输入新密码" auto-complete="off" oncopy="return false;" onpaste="return false;" oncut="return false;"></el-input>
                </el-form-item>
                <el-form-item label="确认密码" prop="renewpassword">
                    <el-input type="password" v-model="ruleForm.renewpassword" placeholder="请再次输入新密码" auto-complete="off" onpaste="return false;" oncopy="return false;" oncut="return false;"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="resetForm">取 消</el-button>
                <el-button type="primary" @click="submitForm">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import r from '../../static/js/RequestUtil.js'
    import Cookie from '../../static/js/cookie.js'
    import MD5 from 'crypto-js/md5';
    export default {
        data() {
            return {
                loginName: 'hello',
                modifyPadDialog: false,
                ruleForm: {
                    oldpassword: '',
                    newpassword: '',
                    renewpassword: '',
                },
                rules: {
                    oldpassword: [{
                        required: true,
                        validator: (rule, value, callback) => {
                            let reg = /^[A-Za-z0-9]{6,18}$/;
                            if(!value) {
                                return callback(new Error('请输入旧密码'));
                            } else if(!reg.test(value)) {
                                return callback(new Error('密码长度在6-18位之间'));
                            } else {
                                callback();
                            }
                        },
                        trigger: 'blur'
                    }],
                    newpassword: [{
                        required: true,
                        validator: (rule, value, callback) => {
                            let reg = /^[A-Za-z0-9]{6,18}$/;
                            if(!value) {
                                return callback(new Error('请输入新密码'));
                            } else if(!reg.test(value)) {
                                return callback(new Error('密码长度在6-18位之间'));
                            } else {
                                callback();
                            }
                        },
                        trigger: 'blur'
                    }],
                    renewpassword: [{
                        required: true,
                        validator: (rule, value, callback) => {
                            let reg = /^[A-Za-z0-9]{6,18}$/;
                            if(!value) {
                                return callback(new Error('请再次确认密码'));
                            } else if(value !== this.ruleForm.newpassword) {
                                callback(new Error('两次输入密码不一致!'));
                            } else if(!reg.test(value)) {
                                return callback(new Error('密码长度在6-18位之间'));
                            } else {
                                callback();
                            }
                        },
                        trigger: 'blur'
                    }],
                }
            }
        },
        methods: {
            //修改密码弹框
            modifyPassword() {
                this.modifyPadDialog = true;
            },
            //提交
            submitForm() {
                this.$refs.ruleForm.validate((valid) => {
                    if(valid) {
                        alert('submit!');
                    } else {
                        console.log('error submit!!');
                        return false;
                    }
                });
            },
            //取消  重置
            resetForm() {
                this.modifyPadDialog = false;
                this.$refs.ruleForm.resetFields();
            },
            handleOpen(key, keyPath) {
                console.log(key, keyPath);
            },
            handleClose(key, keyPath) {
                console.log(key, keyPath);
            },
            //退出登录
            loginOut() {

            }
        },
        mounted() {
            let _this = this;
            let userInfo = JSON.parse(Cookie.get('userInfo'));
            this.loginName = userInfo.loginName;
            r.post("spring-mvc/common/findMenu.do", function(data) {
                if(data && data.code === 0) {
                    console.log(data);
                } else {
                    _this.$router.replace('login');
                    Cookie.del('userInfo');
                }
            })
        }
    }
</script>

<style type="text/css">
    .el-menu-vertical-demo .el-submenu .el-menu-item {
        min-width: 175px;
    }
</style>
<style scoped="scoped">
    .ml50 {
        margin-left: 50px;
    }

    .mr20 {
        margin-right: 20px;
    }

    .main {
        min-width: 1200px;
    }

    .right-content {
        position: absolute;
        right: 60px;
        top: 0;
    }
</style>


